.App {
  text-align: center;
  height: 100vh;
  width: 100vw;
}

.bar-container-container {
  display: flex;
  height: 80%;
  width: 100%;
  background-color: darkslategrey;
  flex-direction: column;
}

.bar-container {
  display: flex;
  margin-top: auto;
  margin-left: auto;
  margin-right: auto;
  width: 99%;
  height: 96%;
  float: left;
}

.bar {
  margin-top: auto;
  height: 10px;
  width: 5px;

  margin-left: 2px;
  margin-right: 2px;
  background-color: skyblue;
  display: inline-block;
}

.menu {
  height: 20%;
  background-color: seagreen;
  width: 100%;
}

.slider {
  opacity: 0.7;
}

.slider:hover {
  opacity: 1;
}

